/* SignInPage.css */

.clsMainCnt {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
    padding-top: 15%;
}

.clsSignInBox {
    width: 90%;
    /* Adjusted for responsiveness */
    max-width: 350px;
    /* Added max-width for larger screens */
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    background-color: #fff;
}

.clsSignInBox h2 {
    margin-bottom: 20px;
    text-align: center;
}

.clsSigninForm {
    margin-bottom: 20px;
}

label {
    display: block;
    margin-bottom: 5px;
}

.clsMainCnt .clsManaulSignIn {
    padding: 10px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s;
    display: flex;
    align-items: center;
}

button.clsGoogleSignIn {
    padding: 10px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s;
}


button.google-btn:hover {
    background-color: #0056b3;
}

.options {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
}

.options a {
    text-decoration: none;
    color: #007bff;
}

.signup-link {
    text-align: center;
}

.signup-link a {
    text-decoration: none;
    color: #007bff;
}

/* Media Queries for Responsiveness */

@media screen and (max-width: 768px) {
    .clsSignInBox {
        width: 80%;
        /* Adjusted width for smaller screens */
    }
}

@media screen and (max-width: 576px) {
    .clsSignInBox {
        width: 90%;
        /* Further adjusted width for extra small screens */
    }
}